import React from 'react'
import '../assets/css/Login.css'
import Button from '../components/button/button.js'
import Input from '../components/input/Input'
import { Link } from 'react-router-dom'
import store from '../plugins/rdeux';
import { checkUser } from '../store/action'
export default class Login extends React.Component {
  state = {
    username: "",
    password: "",
    mess: ""
  }
  render() {
    return (
      <div className="content">
        <p className="fhbtn"><a onClick={() => this.props.history.go(-1)}></a></p>
        <h1 id="h1"></h1>
        <div className="login-box">
          <p className="lsolid"></p>
          <div className="login">
            <Link to="/login">登录</Link>
            <span></span>
            <Link to="/reg">注册</Link>
          </div>
          <p className="rsolid"></p>
        </div>
        <ul>
          <Input
            name="username"
            type="text"
            label="账号"
            value={this.state.username}
            onChange={this.changeIpt}
          />
          <Input name="password" type="text" label="密码" value={this.state.password} onChange={this.changeIpt} />
          <li>{this.state.mess}</li>
        </ul>
        <div className="footbox">
          <Button value='登录' clickHanlder={this.login} />
          <a className="tishi">忘记密码？</a>
        </div>
      </div>
    )
  }
  changeIpt = (ev) => {
    this.setState({
      [ev.target.name]: ev.target.value
    })
  }

  login = () => {
    // console.log('login',this.state.username, this.state.password)
    store.dispatch(checkUser({
      api:'login',
      method:'post',
      username:this.state.username,
      password:this.state.password
    })).then(
      data=>{
        if (data.err===1){
          this.setState({mess:data.msg})
        } else {
          window.localStorage.setItem('react_news_app_uid',JSON.stringify(data));
          this.props.history.push('/user')
        }

      }
    )

    /*React.axios({
      url:'/mock/login',
      params:{
        username:this.state.username,
        password:this.state.password
      }
    }).then(
      res=>{
        if (res.data.err===1){
          this.setState({mess:res.data.msg})
        } else {
          alert('登录成功')
          this.props.history.push('/user')
        }
      }
    )*/
  }
}


