import React from 'react'
import '../assets/css/Home.css'
import Cell from '../components/cell/cell'
import Swiper from '../components/swiper/Swiper'
import { update } from '../store/action'
import *  as types from '../store/types'
import {connect} from 'react-redux';
class Home extends React.Component {

    constructor(props) {
        super()
        props.getHome()
        props.getBanner()
    }
    render() {

        let { banner,home } = this.props
        return (
            <div className="Home">
                <Swiper data={banner} />
                {
                    home.map(item => (
                        <Cell key={item.id} item={item} dataName="home" />
                    ))

                }
            </div>
        )
    }
}
const initMapStateToProps=state=>({
    banner:state.banner,
    home:state.home
  });
  
  const initMapDispatchToProps=dispatch=>({
    getHome:()=>{
      dispatch({type:'CLEAR_HOME'});
      dispatch(update({
        api:'home',
        type:types.UPDATE_HOME,
        params:{
          _limit:5
        }
      }))
    },
    getBanner:()=>dispatch(update({
      api:'banner',
      type:types.UPDATE_BANNER,
      _limit: '5'
    }))
  });

export default connect(initMapStateToProps, initMapDispatchToProps)(Home)
