import React from 'react'
import '../assets/css/Column.css'
import Cell from '../components/cell/cell'
import {update} from '../store/action'
import * as types from '../store/types'
import  connect  from "react-redux/es/connect/connect"
class Column extends React.Component {


    componentDidMount() {
        this.props.getColumn()
    }
    render() {
        return (
            <div className="Column">

                {
                    this.props.list.map(item => (
                        <Cell key={item.id} index={false} item={item} dataName="Column" />
                    ))

                }
            </div>
        )
    }
}
const initMapStateToProps = state =>({
    list:state.column
})
const initMapDispatchToProps = dispatch =>({
    getColumn:()=>dispatch(update({
        api:'column',
        type:types.UPDATE_COLUMN
    }))
})
export default connect(initMapStateToProps,initMapDispatchToProps)(Column)