import React from 'react'
import style from '../assets/css/User.module.css'
import store from '../plugins/rdeux'

export default class User extends React.Component {
  logout = () => {
    React.axios({
      url: '/api/logout',
      method: 'delete'
    }).then(
      res => {
        if (res.data.err === 0) {
          window.localStorage.removeItem('react_news_app_uid')
          store.dispatch({ type: 'CLEAR_USER' })
        }
      }
    )
  }
  render() {
    let {data} = this.props
    return (
      <div className={style.content}>
        <div className={style.header}>
          <h2><img src={`http://localhost:3000` + data.icon} alt="" /></h2>
          <div className={style["user-box"]}>
            <a>{data.nikename}</a>
            <a onClick={this.logout}>注销</a>
          </div>
          <ul className={style.clear}>
            <li>
              <span>0</span>
              <p>关注</p>
            </li>
            <li>
              <span>0</span>
              <p className={style.end}>粉丝</p>
            </li>
          </ul>
        </div>
        <div className={style.docList}>
          <ul>
            <li className={style['gk-text']}>
              <i></i>
              <p>公开博文</p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style["mm-text"]}>
              <i></i>
              <p>秘密博文</p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style["cg-text"]}>
              <i></i>
              <p>草稿箱</p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style["sc-text"]}>
              <i></i>
              <p>收藏夹</p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style["my_cz"]}>
              <i></i>
              <p>收藏夹</p>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

