import React from 'react'
import '../assets/css/Detail.css'
import xing from '../assets/img/xing.png'
import querystring from 'query-string'
import date from "../utils/date";
import {update} from '../store/action'
import * as types from '../store/types'
import connect from "react-redux/es/connect/connect";
 class Detail extends React.Component {
   
    goback = () => {
        this.props.history.go(-1)
    }
    componentDidMount() {
        let id = this.props.match.params.id
        let dataName = querystring.parse(this.props.location.search).dataName
       this.props.getDetail(dataName,id)
    }
    render() {
        let { detail } = this.props
        /*  let MockStyleStr = '<span style="background:red">xxx</span>'*/
        let el = null
        if (detail.detail) {
            el = (
                <div className="Detail">
                    <div className="nav">
                        <ul>
                            <li className="l-btn" onClick={this.goback}></li>
                        </ul>
                    </div>
                    <div className="content">
                        <div className="header clear"><h2><img src={detail.detail.auth_icon} alt="" /></h2><p>作者名字</p></div>
                        <div className="cont">
                            <h3>{detail.title}</h3>
                            <div className="time"><p>{date(detail.time)}<span><img src="src/assets/img/zan.png" alt="" /></span></p></div>
                            <div className="text-box"
                            /*   dangerouslySetInnerHTML={{__html:MockStyleStr + detail.detail.content}} */
                            >{detail.detail.content}</div>
                        </div>
                    </div>
                    <div className="foot-btn">
                        <ul>
                            <li className="say"><a href="#">
                                <i></i><span>0</span>
                            </a></li>
                            <li className="zan"><a href="#">
                                <i></i><span>0</span>
                            </a></li>
                            <li className="xing"><a href="#">
                                <i><img src={xing} alt="" /></i>
                            </a></li>
                            <li className="fx"><a href="">
                                <i><img src={require('../assets/img/fx.png')} alt="" /></i>
                            </a></li>
                        </ul>
                    </div>
                </div>
            )
        } else {
            el = <div>骨架屏</div>
        }
        return el;
    }
}
const initMapStateToProps = state =>({
    detail:state.detail
})
const initMapDispatchToProps = dispatch =>({
    getDetail:(api,id)=>dispatch(update({
        api,
        type:types.UPDATE_DETAIL,
        id
    }))
})
export default connect(initMapStateToProps,initMapDispatchToProps)(Detail)

