import * as types from './types'
export default (state, { type, payload }) => {//负责突变state和获取state 要求一定要获取到state
    switch (type) {
        case types.VIEW_NAV:
            return { ...state, bNav: payload }
        case types.VIEW_FOOT:
            return Object.assign({}, state, { bFoot: payload })//对象浅合并
        case types.VIEW_LOADING:
            let newState = { ...state }
            newState.bLoading = payload
            return newState
        case types.UPDATE_HOME:
            return { ...state, home: payload }
        case types.CLEAR_HOME:
            return { ...state, home: [] }
        case types.UPDATE_COLUMN:
            return { ...state, column: payload }
        case types.UPDATE_FOLLOW:
            return { ...state, follow: payload }
        case types.UPDATE_BANNER:
            return { ...state, banner: payload };
        case types.UPDATE_DETAIL:
            return { ...state, detail: payload }
        case types.CHECK_USER:
            return { ...state, user: payload }
        case types.CLEAR_USER:
            return {
                ...state, user: {
                    err: 1,
                    msg: "未登录",
                    data: {}
                }
            }
        default:
            return state;
    }
}