import React from 'react'
import style from '../assets/css/Footer.module.css'
import {NavLink}from 'react-router-dom'
export default class Footer extends React.Component{
    render(){
        return(
          <div className={style.foot}>
            <div className={style['foot-btn']}>
            <ul>
              <li className={style.home}><NavLink to="/home" activeClassName={style.home_active}/></li>
              <li className={style.write}><NavLink to="/shopcart" activeClassName={style.write_active}/></li>
              <li className={style.my}><NavLink to="/user" activeClassName={style.my_active}/></li>
            </ul>
          </div>
          </div>
        )
    }
}

