import React from 'react';
import './button.css';
import propTypes from 'prop-types'

function Button(props){
  return(
    <input
    type = 'button'
    value={props.value}
    className='login-btn'
    onClick={props.clickHanlder.bind(null,12)}
    />
  )
}
Button.defaultProps={
  value:'按钮',
  clickHanlder:()=>{}
};
Button.propTypes={
  value:propTypes.string,
  clickHanlder:propTypes.func
}
export default Button
