import React from 'react'
import './cell.css'
import { withRouter } from 'react-router-dom'

let goDetail = (id, dataName, history) => {
    history.push({ pathname: '/detail/' + id, search: 'dataName=' + dataName })
}

export default withRouter(({ item, dataName, history, index = true }) => (
    <div className={'Cell'} onClick={() =>goDetail(item.id, dataName,history)}>
      {
          index ?   <h2>{item.id}.{item.title}</h2> :  <h2>{item.title}</h2>
      }
        <p>{item.des}</p>
    </div>
))


