import React from 'react'
import '../assets/css/Follow.css'
import Cell from '../components/cell/cell'
import { update } from "../store/action";
import * as types from "../store/types";
import connect from "react-redux/es/connect/connect";
class Follow extends React.Component {


    componentDidMount() {
        this.props.getFollow()
    }
    render() {

        return (
            <div className="Follow">

                {
                    this.props.follow.map(item => (
                        <Cell key={item.id} item={item} dataName="Follow" />
                    ))

                }
            </div>
        )
    }
}
const initMapStateToProps = state => ({
    follow: state.follow,

});

const initMapDispatchToProps = dispatch => ({
    getFollow: () => dispatch(update({
        api: 'follow',
        type: types.UPDATE_FOLLOW
    }))

});

export default connect(initMapStateToProps, initMapDispatchToProps)(Follow)
